html, body {
	position: relative;
	max-width: 100vw;
	scroll-behavior: smooth;
}

html.openMenu {
	height: 100vh;
	max-height: 100vh;
	overflow-y: hidden;
}

.container.grey, .container-fluid.grey {
	background-color: #f2f2f2;
}

.special-title {
	font-size: 40px;
	font-style: normal;
	font-weight: 600;
	line-height: 56px;
	letter-spacing: 0px;
	&.bigger {
		font-size: 45px;
	}
}
.special-subtitle {
	font-size: 16px;
	font-style: normal;
	font-weight: 600;
	line-height: 30px;
	letter-spacing: 0px;
}

.special-small-text {
	font-size: 15px;
	font-style: normal;
	font-weight: 300;
	line-height: 20px;
	letter-spacing: -0.3px;
}

